::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #22d3ee 0%, #10b981 100%);
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: content-box;
  box-shadow: inset 0 0 6px rgba(34, 211, 238, 0.3);
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #06b6d4 0%, #059669 100%);
  background-clip: content-box;
  box-shadow: inset 0 0 8px rgba(34, 211, 238, 0.5);
}
::-webkit-scrollbar-thumb:active {
  background: linear-gradient(180deg, #0891b2 0%, #047857 100%);
  background-clip: content-box;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
